@import "./Calendar.css";

:root {
  --main-color: #EEECDA;
  --secondary-color: #D3A6AD;
  --darken-secondary-color: #c2848e;
}

html {
  height: 100%;
}

body {
  font-family: 'Fira Sans', sans-serif;
  background: -webkit-gradient(linear, left top, left bottom, from(var(--main-color)), to(var(--secondary-color)));
  background: -webkit-linear-gradient(var(--main-color), var(--secondary-color));
  background: -o-linear-gradient(var(--main-color), var(--secondary-color));
  background: linear-gradient(var(--main-color), var(--secondary-color));
}

.main-wrapper .title {
  text-align: center;
  color: #a78489;
}


